* {
  margin: 0;
  padding: 0;
}

body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

.stopwatch__min {
  z-index: 0;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) 3600s infinite watch;
  position: absolute;
  top: 20vmin;
  left: 38.5vmin;
}

.stopwatch__sec {
  z-index: 1;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite watch;
  position: absolute;
  top: 2vmin;
  left: 39.25vmin;
}

.stopwatch__center {
  z-index: 2;
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
  top: 37.5vmin;
  left: 37.5vmin;
}

.stopwatch--speed-up .stopwatch__min {
  animation-duration: 100s;
  animation-delay: 10s;
}

.stopwatch--speed-up .stopwatch__sec {
  animation-duration: 10s;
}

@keyframes watch {
  from {
    transform-origin: bottom;
    transform: rotate(0);
  }

  to {
    transform-origin: bottom;
    transform: rotate(360deg);
  }
}
